.app {
    max-width: 430px;
    margin: 2rem auto;
    text-align: center;
    color: #fff;
    &__header {
        margin-bottom: 2rem;
        .btn {
            margin-bottom: 1rem;
            font-size: .75rem;
            @include q(600) {
                font-size: 1rem; }
            @include q(900) {
                font-size: 1.125rem; } } }
    &__body {
        margin-bottom: 2rem; }
    &__text {
        font-size: 2rem;
        text-shadow: 1px 1px 3px #333;
        font-weight: 700; }
    &__footer {
        display: flex;
        justify-content: space-between; } }



