.page-title__text {
  position: relative;
  padding: 3.5rem 0;
  display: inline-block;
  z-index: 100;
}

.page-title__bg {
  z-index: 10;
  position: absolute;
  font-size: 2.8em;
  line-height: 1;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #f6f6f6;
}

@media only screen and (min-width: 1400px) {
  .page-title__bg {
    left: -12%;
  }
