}

.progress-bar {
  padding: 50px 0;
}

.progress-bar__row {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.progress-bar__row:before {
  position: absolute;
  content: '';
  height: 8px;
  background-color: #f6f6f6;
  top: 0;
  bottom: 0;
  left: 10px;
  right: 10px;
  margin: auto;
}

.progress-bar__item {
  flex: 1;
  display: flex;
  position: relative;
}

.progress-bar__item:last-child {
  flex: 0;
}

.progress-bar__item:last-child .progress-bar__date, .progress-bar__item:last-child .progress-bar__event {
  right: 0;
  left: auto;
}

.progress-bar__btn {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #333;
  position: relative;
  border: 2px solid #333;
}

.progress-bar__btn:after {
  position: absolute;
  content: '';
  border: 2px solid #333;
  left: -50%;
  top: -50%;
  margin: auto;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transform: scale(0);
  transition: transform 0.3s cubic-bezier(0.09, 0.26, 0.52, 1.95);
}

.progress-bar__btn.is-checked:after {
  transform: scale(1);
}

.progress-bar__event {
  position: absolute;
  left: 0;
  bottom: 150%;
  font-size: .875rem;
  font-weight: bold;
  max-width: 90px;
}

.progress-bar__date {
  position: absolute;
  left: 0;
  top: 150%;
  font-weight: bold;
  font-size: 0.875rem;
  color: #aaa;
}

