.toggler {
  border: none;
  position: relative;
  padding: 1.25rem;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

@media only screen and (min-width: 900px) {
  .toggler {
    display: none;
  }
}

.toggler__item {
  position: absolute;
  height: 2px;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  width: 80%;
  margin: auto;
  border-radius: 3px;
  background-color: #fff;
  transition: .4s;
}

.toggler__item:nth-child(1) {
  transform: translateY(-10px);
}

.toggler__item:nth-child(2) {
  opacity: 1;
}

.toggler__item:nth-child(3) {
  transform: translateY(10px);
}

.toggler.is-active .toggler__item:nth-child(1) {
  transform: rotate(-45deg);
}

.toggler.is-active .toggler__item:nth-child(2) {
  opacity: 0;
}

.toggler.is-active .toggler__item:nth-child(3) {
  transform: rotate(45deg);
}

