.nav-links a {
  width: 100%;
  display: inline-block;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #525252;
  font-weight: 700;
}

@media only screen and (min-width: 900px) {
  .nav-links a {
    border-bottom: none;
    position: relative;
  }
  .nav-links a:after {
    position: absolute;
    content: '';
    border-bottom: 5px solid #faac09;
    bottom: -2px;
    left: .8rem;
    right: .8rem;
    transform: scaleX(0);
    transition: transform .3s ease-in-out;
  }
  .nav-links a:hover:after {
