  left: .9rem;
  right: .9rem;
  border-top: 2px solid #faac09;
  transform-origin: left;
  transform: scaleX(0.5);
  transition: transform .3s ease-in-out;
}

.social-links .nav-item a:after {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: .9rem;
  right: .9rem;
  border-bottom: 2px solid #faac09;
  transform-origin: right;
  transform: scaleX(0.5);
  transition: transform .3s ease-in-out;
}

.social-links .nav-item a:hover:before {
  transform: scaleX(1);
}

.social-links .nav-item a:hover:after {
  transform: scaleX(1);
}

