.btn {
  display: inline-block;
  font-weight: 700;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 2px solid #efefef;
  padding: .575rem 1.625rem;
  color: #333;
  background-color: transparent;
  font-size: 0.875rem;
  text-transform: uppercase;
  line-height: 1.5;
  transition: all .15s;
}

.btn:hover {
  text-decoration: none;
}

.btn--primary {
  background-color: rgba(51, 51, 51, 0.75);
}

.btn--secondary {
  background-color: rgba(250, 172, 9, 0.75);
}

.btn--transition {
  position: relative;
}

.btn--transition:after {
  content: '';
  position: absolute;
  top: -2px;
  right: -2px;
  width: 50%;
  height: 50%;
  border-top: 2px solid #faac09;
  border-right: 2px solid #faac09;
  transition: .3s;
}

.btn--transition:before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: -2px;
  width: 50%;
  height: 50%;
  border-bottom: 2px solid #faac09;
  border-left: 2px solid #faac09;
  transition: .2s;
}

.btn--transition:hover:before {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
}

.btn--transition:hover:after {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
}

.btn--block {
  width: 100%;
  display: block;
}

.btn--lg {
  padding: 1.125rem 1rem;
  font-size: 1.125rem;
}

.btn--light {
  color: #333;
}

.btn--dark {
  color: #fff;
}

.btn--close {
  border: none;
  position: relative;
  width: 50px;
  height: 50px;
  padding: 10px;
}

.btn--close:after {
  position: absolute;
  content: '';
  height: 2px;
  background-color: #efefef;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  transform: rotate(45deg);
}

.btn--close:before {
  position: absolute;
  content: '';
  height: 2px;
  background-color: #efefef;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  transform: rotate(-45deg);
}

