.dropdown {
  position: relative;
}

.dropdown__menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1000;
  display: none;
  font-size: 1rem;
  color: #fff;
  font-weight: bold;
  text-align: left;
  list-style: none;
  background-color: #333;
  background-clip: padding-box;
}

.dropdown__item {
  display: block;
  width: 100%;
  text-align: center;
  white-space: nowrap;
  padding: .625rem 1.25rem;
}

.dropdown__toggle {
  padding: .625rem 1.25rem;
  border: none;
  position: relative;
}

.dropdown__toggle:after {
  position: absolute;
  content: '';
  width: 6px;
  height: 6px;
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  transform-origin: center top;
  transform: rotate(45deg);
  top: 0;
  right: 5px;
  bottom: 0;
  margin: auto;
}
