.progress-bar {
    padding: 50px 0;
    &__row {
        display: flex;
        justify-content: space-between;
        position: relative; }
    &__row:before {
        position: absolute;
        content: '';
        height: 8px;
        background-color: $gray-5;
        top: 0;
        bottom: 0;
        left: 10px;
        right: 10px;
        margin: auto; }
    &__item {
        flex: 1;
        display: flex;
        position: relative; }
    &__item:last-child {
        flex: 0;
        .progress-bar__date, .progress-bar__event {
            right: 0;
            left: auto; } }
    &__btn {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: $bg-dark;
        position: relative;
        border: 2px solid $bg-dark; }
    &__btn:after {
        position: absolute;
        content: '';
        border: 2px solid $bg-dark;
        left: -50%;
        top: -50%;
        margin: auto;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        transform: scale(0);
        transition: transform .3s cubic-bezier(0.09, 0.26, 0.52, 1.95); }
    &__btn.is-checked:after {
        transform: scale(1); }
    &__event {
        position: absolute;
        left: 0;
        bottom: 150%;
        $color: $base-font-color;
        font-size: .875rem;
        font-weight: bold;
        max-width: 90px; }
    &__date {
        position: absolute;
        left: 0;
        top: 150%;
        font-weight: bold;
        font-size: 0.875rem;
        color: $disable-color; } }


.grid-item {
    margin-bottom: 30px; }

.grid-item,
.grid-sizer {
    width: 100%;
    @include q(600) {
        width: 48%; }
    @include q(1000) {
        width: 32%; } }

.gutter-sizer {
    width: 2%; }



.card {
    background-color: #fff;
    box-shadow: 0px 3px 6px 0px rgba(0, 11, 10, 0.05);
    &__header {
        padding-bottom: 1rem;
        padding-top: 2rem; }
    &__header-text {
        display: inline-block;
        width: 72%;
        padding: 0 1.875rem;
        background-color: #faac03;
        color: #fff;
        font-size: .875rem;
        font-weight: 700;
        text-transform: uppercase; }
    &__body {
        padding: 1.5rem 1.875rem;
        padding-top: 0; }
    &__body-title {
        color: #333333;
        font-weight: 700;
        font-size: 1.375rem;
        text-transform: uppercase;
        margin-bottom: 1rem; }
    &__body-text {
        color: #868686;
        font-weight: 300;
        font-size: 1rem; } }
